/* main */
main {
  min-height: calc(100vh - 260px);
}
.productImage {
    width: 300px; /* Adjust the width as needed */
    height: 300px; /* Adjust the height as needed */
    object-fit: cover;
}

.productButton{
    background-color: #0d6efd;
}
.product-list {
  padding-top: 24px;
}

.product-list .container {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.product-list .container h2 {
  margin-bottom: 16px;
  font-size: 1.3rem;
  font-weight: 600;
  color: #333; /* Replace with the appropriate color value from your $clr-dark variable */
}

.product-list .container h3 {
  margin-bottom: 16px;
  font-size: 1.1rem;
  font-weight: 500;
  color: #333; /* Replace with the appropriate color value from your $clr-dark variable */
}

.product-list .container .flex-row {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 16px;
  gap: 16px;
}

.cart {
  min-width: 20%;
  max-width: 30%;
  max-height: 60%;
  background-color: var(--light);
  overflow: auto;
  padding: .5rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
  border-bottom-left-radius: .5rem;
}

.cart h2 {
  font-size: 1.5rem;
  border-bottom: 1px solid var(--dark);
  padding-bottom: .5rem;
  margin: 1rem 0;
}

.cart .close {
  position: absolute;
  top: .5rem;
  right: .5rem;
  cursor: pointer;
  color: var(--tertiary);
}

.cart .close:hover {
  text-decoration: underline;
  cursor: pointer;
}

.cart-closed {
  font-size: 2rem;
  cursor: pointer;
  background-color: var(--secondary);
  border-radius: 50%;
  padding: .25rem;
  width: 50px;
  height: 50px;
}

.cart-closed:hover {
  -webkit-transform: rotate(8deg);
          transform: rotate(8deg);
}

.cart img {
  width: 70px;
  margin-right: 1rem;
}

.cart input {
  width: 50px;
  padding: 0;
  margin: 0 .5rem 1rem 0;
}
.close{
  cursor: pointer;
}


.cart-toggle {
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  top: 0;
  right: 0;
  z-index: 100;
}

.cart-modal {
  position: fixed;
  top: 0;
  right: 0;
  width: 20%;
  height: 100%;
  background-color: white;
  overflow-y: auto;
  z-index: 10;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.2);
}

.cart-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: flex-end;
}

.cart-panel {
  width: 20%;
  height: 100%;
  background-color: white;
  padding: 1rem;
  box-sizing: border-box;
  overflow-y: auto;
}

.cart-items-count {
  position: absolute;
  top: -5px;
  right: -10px;
  background-color: red;
  color: white;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .cart {
    max-width: 100%;
    max-height: auto;
    box-shadow: none;
    background-color: inherit;
  }

  .cart-closed {
    margin: 0 auto;
  }

  .navbar-collapse {
    justify-content: center;
  }

  .navbar .navbar-collapse .me-auto,
  .navbar .navbar-collapse .ms-auto {
    margin: 0;
  }

  .cart-toggle {
    top: 0;
    right: 0;
  }
  .cart-modal {
    position: fixed;
    top: 0;
    right: 0;
    width: 80%;
    height: 100%;
    background-color: white;
    overflow-y: auto;
    z-index: 10;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.2);
  }
}
.carouselProductItem {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .carouselProductItemImage {
    width: 30vh; 
    height: 30vh; 
    object-fit: cover;
    }

  
  .carouselProductItemInfo {
    color: white;
    max-width: 50%;
    text-align: left;
  }
  
.carousel-item-custom {
  margin-top: 10vh;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 0.5rem;
    padding: 1rem;
  }

  .carousel-control-prev,
.carousel-control-next {
  display: none;
}

  
.sc-login {
  padding-top: 60px;
}

.sc-login .login-content {
  background-color: #ffffff;
  max-width: 420px;
  margin-right: auto;
  margin-left: auto;
  border-radius: 6px;
}

.sc-login .login-content .login-title {
  color: #555555;
  text-align: center;
  margin-bottom: 20px;
}

.sc-login .login-content .form-element {
  margin-bottom: 12px;
}

.sc-login .login-content .form-element .form-label {
  color: #333333;
  margin-bottom: 8px;
  display: block;
}

.sc-login .login-content .form-element .form-control {
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  transition: all 300ms ease-in-out;
  font-size: 14px;
  padding-top: 8.5px;
  padding-bottom: 8.5px;
}

.sc-login .login-content .form-element .form-control:focus {
  border-color: #CF363B;
}

.sc-login .login-content .btn-login {
  margin-top: 12px;
  background-color: #CF363B;
  width: 100%;
  border-radius: 4px;
  padding: 12px 16px;
  color: #ffffff;
}

.login {
